
.open-nav, .overlay {
  display: none;
}



.header{
  z-index: 10;
  padding: 40px 0 0 0;
  &__wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .logo{
    width: 96px;
    height: auto;
    order: 1;
  }

  .wrap-nav{
    order: 2;
  }

  .top-btn{
    order: 3;
  }



  .menu{
    display: flex;
    align-items: center;
    &> * + *{
      margin-left: 40px;
    }
    a{
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 22px;
      color: #000000;
      will-change: transform;
      &:hover{
        color: $yellow;
      }
    }
  }

}

body.open{
  height: 100vh;
  overflow: hidden;
}