

.button{
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 55px;
  text-align: center;
  color: #FFFFFF;
  width: fit-content;
  background: rgba(40, 71, 92, 0.8);
  border-radius: 10px;
  transition: $tr;
  padding: 0 85px;

  &:hover, &.active{
    background: #28475C;
  }

  &-header{
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    color: #000000;
    padding: 8px 38px;
    border: 2px solid #F7BD32;
    box-sizing: border-box;
    border-radius: 10px;
    background: transparent;
    &:hover, &.active{
      background: $yellow;
      color: #fff;
    }
  }

}


.welcome{

  padding: 225px 0 65px;
  margin-top: -105px;


  &__img{
    position: absolute;
    right: 0;
    max-width: 47%;
    height: 85%;
    top: 50%;
    transform: translateY(-50%);

    display: flex;
    align-items: center;
    justify-content: center;

    & img{
      max-height: 100%;
    }
  }

  &__wrap{
    display: flex;
    flex-direction: column;

  }
  &__text{
    max-width: 490px;
    display: flex;
    flex-direction: column;
    h5{
