  //  background: radial-gradient(46.91% 46.91% at 66.93% 58.07%, #E7E7E7 0%, rgba(218, 228, 234, 0.65) 100%);
  //  filter: blur(300px);
  //  right: -350px;
  //  top: -350px;
  //  width: 900px;
  //  height: 900px;
  //  border-radius: 50%;
  //}

  &:before{
    content: '';
    position: absolute;
    background: url("../img/welcome-blur.png") center no-repeat;
    background-size: cover;
    width: 756px;
    height: 994px;
    top: -10px;
    right: -10px;
  }
}


.title-wrap{
  text-align: center;
  &> * + *{
    margin-top: 15px;
  }
  p{
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 150%;
