    width: 100%;
    z-index: 2;
    &:before{
      content: '';
      position: absolute;
      background: url("../img/about/about-img-decor.svg") center no-repeat;
      background-size: cover;
      width: 73%;
      height: 163%;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
    &:after{
      content: '';
      position: absolute;
      background: url("../img/about/about-img-decor-small.svg") center no-repeat;
      background-size: cover;
      width: 100px;
      height: 70px;
      left: 0;
      top: -35px;
      z-index: 5;
