      align-items: center;
      span{
        font-weight: 200;
        font-size: 65px;
        line-height: 150%;
        color: #28475C;
        b{
          font-weight: normal;
          font-size: 90px;
          line-height: 150%;
          color: #168B9C;
        }
      }
      &> * + *{
        padding-left: 20px;
      }
    }
    &> * + *{
      margin-top: 60px;
    }
    p{
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 150%;
      color: #707070;
    }
