.symptoms{
  padding: 60px 0 145px;
  margin-top: -40px;

  &__wrap{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__img{
    height: 405px;
    display: flex;
    align-items: center;
    justify-content: center;
    & img{
      max-height: 100%;
    }
  }
  &__item{
    display: flex;
    flex-direction: column;
    max-width: 510px;
    &> * + *{
      margin-top: 16px;
    }
  }
  &__txt{
    &> * + *{
      margin-top: 15px;
    }
    p{
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 150%;
      color: #707070;
    }
  }
  &__video{
    max-width: 785px;
    width: 100%;
    margin: 120px auto 0;
    &-overlay{
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: 99;
      &> img{
        @include img-size;
      }
      &.active{
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        .play-button{
          opacity: 0;
          visibility: hidden;
          pointer-events: none;
        }
      }
    }
    &-wrap{
      display: none;
      margin-top: 45px;
      border-radius: 10px;
      overflow: hidden;
      & *{
        transition: $tr;
      }
      &:before{
        content: '';
        display: block;
        padding-top: 62%;
      }
      & iframe{
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
      }
    }
  }
  .title-wrap{
    &> * + *{
      margin-top: 15px;
    }
  }
}

.play-button{
  width: 95px;
  height: 95px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1.5px solid #FFFFFF;
  border-radius: 50%;
  & img{
    max-width: 100%;
    max-height: 100%;
  }
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.list-dotted{
  &> * + *{
    margin-top: 5px;
  }
  li{
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 150%;
    color: #707070;
    padding-left: 23px;
    &:before{
      content: '';
      position: absolute;
      border-radius: 50%;
      width: 10px;
      height: 10px;
      background: transparent;
      border: 1px solid #128475;
      left: 0;
      top: 5px;
    }
  }
}


