.link-download{
  font-style: italic;
  font-weight: normal;
  font-size: 12px;
  line-height: 150%;
  letter-spacing: -0.25px;
  text-decoration-line: underline;
  color: rgba(112, 112, 112, 0.8);
  padding-left: 23px;
  text-align: left;
  width: fit-content;
  &:before{
    content: '';
    position: absolute;
    background: url("../img/icons/download-icon.svg") center no-repeat;
    background-size: cover;
    width: 17px;
    height: 17px;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  &:hover{
    text-decoration: none;
  }
}

.why{
  padding: 75px 0 105px;
  z-index: 2;
  &--icons{
    display: none;
  }
  &:before{
    content: '';
    position: absolute;
    height: 10%;
    width: 100%;
    bottom: 0;
    left: 0;
    background: #F1FAFF;
  }
  &__table{
    margin: 90px 0 0;



    &--icon{
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 auto;
      & img{
        max-width: 100%;
        max-height: 100%;
      }
    }
    table{
      border-collapse: separate;
      border-spacing: 0;


      .h5{
        font-weight: 700;
        text-align: center;
      }
      .h3{
        font-weight: 700;
      }
      thead{
        th{
          padding: 10px 5px;
          &:nth-last-child(2){
            border: 2.5px solid rgba(47, 136, 134, 0.62);
            border-radius: 10px 10px 0 0;
            border-bottom: none;
          }
          &:last-child{
            //padding: 0 50px 10px 5px;
          }
        }
      }
      tbody{
        tr{
          td{
            &:first-child{
              border-radius: 25px 0 0 25px;
            }
            &:last-child{
              border-radius: 0 25px 25px 0;
              //padding: 0 100px 25px 0;
            }
            &:nth-last-child(2){
              border: 2px solid rgba(47, 136, 134, 0.62);
              border-top: none;
              border-bottom: none;
            }
          }
          &:nth-child(odd){
            td{
              background: #ffff;
            }
          }
          &:last-child{
            td:nth-last-child(2){
              border-radius: 0 0 10px 10px;
              border: 2.5px solid rgba(47, 136, 134, 0.62);
              border-top: none;
            }
          }
        }
        td{
          font-style: normal;
          font-weight: normal;
          font-size: 16px;
          line-height: 24px;
          letter-spacing: -0.25px;
          color: #000000;
          text-align: center;

          padding: 15px 30px;

          &:first-child{
            text-align: left;
            font-style: normal;
            font-weight: bold;
            font-size: 15px;
            line-height: 20px;
            letter-spacing: -0.25px;
            color: #28475C;
            font-family: $dm;
          }
          &:last-child{
            font-weight: bold;
            font-size: 14px;
            line-height: 21px;
            color: #28475C;
          }
        }
      }

    }
  }
}


.swiper-button-prev, .swiper-button-next{
  &:after{
    display: none;
  }
  width: 33px;
  height: 33px;
}

.swiper-button-prev{
  &:before{
    content: '';
    position: absolute;
    background: url("../img/icons/arrow-prev.svg") center no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
  }
  left: -50px;
}

.swiper-button-next{
  &:before{
    content: '';
    position: absolute;
    background: url("../img/icons/arrow-prev.svg") center no-repeat;
    transform: scaleX(-1);
    background-size: cover;
    width: 100%;
    height: 100%;
  }
  right: -50px;
}


.swiper-pagination{
  left: 50% !important;
  transform: translateX(-50%);
  &-bullet{
    margin: 0 5px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: transparent;
    border: 1px solid #28475C;
    transform: scale(1) !important;
    &-active{
      background: #28475C;
    }
    //&.swiper-pagination-bullet-active-next, &.swiper-pagination-bullet-active-prev, &.swiper-pagination-bullet-active-next-next, &.swiper-pagination-bullet-active-prev-prev{
    //  transform: scale(1);
    //}
  }
}


.buy{
  padding: 115px 0 55px;
  overflow: hidden;
  margin-top: -8px;
  &:before{
    content: '';
    position: absolute;
    background: #F8F8F8;
    width: 100%;
    height: 10%;
    bottom: 0;
    top: 0;
    z-index: -1;
  }
  &__wrap{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
  }
  &__title{
    max-width: 320px;
    h2{
      line-height: 150%;
    }
    h5{
      margin: 15px 0 55px;
    }
  }
  &__set{
    border: 2px dashed rgba(153, 198, 214, 0.75);
    border-radius: 70px;
    display: flex;
    align-items: center;
    padding: 30px 40px;
    &:before{
      content: '';
      position: absolute;
      background: url("../img/buy/block-decor.svg") center no-repeat;
      background-size: contain;
      width: 41%;
      height: 32%;
      bottom: 100%;
      left: 50%;
      transform: translateX(-50%);
    }
    &-plus{
      margin: 0 20px;
    }
    &-item{
      &:first-child{
        .buy__set-title{
          padding-left: 40px;
        }
      }
    }
    &-img{
      width: 170px;
      height: 220px;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
      & img{
          max-height: 100%;
      }
    }
    &-title{
      margin-bottom: 10px;
      h5{
        line-height: 130%;
        font-weight: 700;
      }
      p{
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 130%;
        color: #707070;
      }
    }
  }
  &__block{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
    margin-top: 55px;
    &-img{
      width: 230px;
      &:before{
        content: '';
        position: absolute;
        background: radial-gradient(34.28% 34.28% at 55.76% 40.82%, rgba(17, 168, 253, 0.364) 0%, rgba(22, 139, 156, 0.245) 100%);
        filter: blur(100px);
        width: 200px;
        height: 200px;
        right: -100px;
        bottom: -20px;
        border-radius: 50%;
      }
    }
    &-content{
      max-width: 620px;
      &:before{
        content: '';
        position: absolute;
        background: url("../img/buy/text-decor.svg") center no-repeat;
        background-size: contain;
        width: 125%;
        height: 165%;
        left: 50%;
        top: 60%;
        transform: translate(-50%, -50%);
      }

    }
    &-info{
      display: flex;
      flex-wrap: wrap;
      margin-top: 30px;
      .text-block{
        width: calc(100% - 80px);
        padding-left: 30px;
      }
    }
    &-col{
      display: flex;
      flex-direction: column;
      width: 80px;
      &> * + *{
        margin-top: 40px;
      }
    }
  }
}

.text-block{
  &> * + *{
