  }
}


.faq{
  padding: 120px 0;
  .title-wrap{
    text-align: left;
    & *{
      line-height: 100%;
    }
  }
  &__img{
    position: absolute;
    right: 0;
    top: 0;
    width: 545px;
    height: 100%;
    text-align: right;
    & img{
      max-height: 100%;
    }
  }
  &__wrap{
    margin-top: 60px;
  }
  &__item{
    border: 1px solid #F7F7F7;
    max-width: 720px;
    border-radius: 10px;
    overflow: hidden;
    transition: $tr;
    & + .faq__item{
      margin-top: 10px;
    }

    &-list{
      margin-top: 5px;
      padding-left: 20px;
      li{
        list-style: square;

        font-weight: normal;
        font-size: 14px;
        line-height: 150%;
        text-transform: lowercase;
      }
    }


    &.active{
      border-radius: 10px 10px 0 0;
      .faq__item-question{
        &:before{
          transform: translateY(-50%) rotate(180deg);
        }
      }
    }
    &-question{
      background: rgba(239, 239, 239, 0.5);
      border-radius: 10px 10px 0px 0px;
      padding: 25px 50px 25px 20px;
      font-style: normal;
      font-weight: bold;
      font-size: 14px;
      line-height: 150%;
      color: #000000;
      cursor: pointer;
      &:before{
        content: '';
        position: absolute;
        background: url("../img/icons/questions-arrow.svg") center no-repeat;
        background-size: cover;
        width: 12px;
        height: 6px;
        top: 50%;
        transform: translateY(-50%);
        right: 30px;
        transition: $tr;
      }
    }
    &-answer{
      padding: 15px 20px;
      border: 1px solid #F7F7F7;
      background: #fff;
      p{
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: #000000;
        b{
          font-weight: 700;
        }
      }

    }
  }
}

