    }
    background: linear-gradient(182.05deg, #DBF2FF 1.54%, rgba(255, 255, 255, 0.12) 91.08%);
    box-shadow: 0px 3px 3px rgba(242, 242, 242, 0.25);
    border-radius: 30px 30px 0px 0px;
    padding: 15px;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    width: max-content;
    max-width: 190px;
  }

  &__swiper{
    margin-top: 55px;
  }
  &__lower{
    padding-top: 180px;
    .title-wrap{
      &__img{
        position: absolute;
        margin: 0;
        right: 0;
        bottom: -30px;
        z-index: -1;
        //&:before{
        //  content: '';
        //  position: absolute;
        //  background: radial-gradient(34.28% 34.28% at 55.76% 40.82%, rgba(72, 156, 203, 0.52) 0%, rgba(140, 193, 223, 0.36) 100%);
        //  filter: blur(80px);
        //  width: 300px;
        //  height: 300px;
        //  border-radius: 50%;
        //  right: -85px;
        //  bottom: -90px;
        //}
        &:before{
          content: '';
          position: absolute;
          background: url("../img/advantages-blur.png") center no-repeat;
          background-size: contain;
          width: 915px;
          height: 915px;
          left: 50%;
          top: 50%;
          transform: translateX(-50%) translateY(-50%);
        }
      }
    }
  }



  &-swiper{
    padding: 0 0 50px;
    .swiper-slide{
      height: auto;
    }
    &__slide{
      padding: 40px 30px;
      background: #FFFFFF;
      border-radius: 10px;
      height: 100%;
      display: flex;
      flex-direction: column;
    }
    &__info{
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      &-txt{
        width: calc(100% - 60px);
        padding-left: 20px;
        h5{
          color: #000000;
          font-weight: 700;
          margin-bottom: 5px;
          line-height: 100%;
          & + small{
            display: block;

            font-style: normal;
            font-weight: 300;
            font-size: 12px;
            line-height: 150%;
            color: #000000;
          }
        }

      }
    }
    &__img{
      width: 60px;
      height: 60px;
      border-radius: 50%;
      border: 2px solid #0e7d6f;
      overflow: hidden;
      & img{
        @include img-size;
      }
    }
    &__txt{
      margin: 38px 0 14px;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 150%;
      color: #28475C;
      flex-grow: 1;
    }
    &__user{
      font-style: italic;
      font-weight: normal;
      font-size: 12px;
      line-height: 150%;
      text-align: right;
      color: #888888;
    }
  }



}

.alert{
  background: #FFFFFF;
  border: 1px solid #168B9C;
  box-sizing: border-box;
  border-radius: 10px;
  display: flex;
  padding: 12px;
  &__icon{
    width: 15px;
    height: 15px;
    background: #168B9C;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;

    font-style: normal;
    font-weight: bold;
    font-size: 10px;
    line-height: 150%;
    color: #FFFFFF;
  }
  &__txt{
    width: calc(100% - 15px);
    padding-left: 6px;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    color: #707070;
    b{
      font-weight: 700;
    }
  }
}

.shops{
  display: flex;
  flex-wrap: wrap;
  margin: 0 -10px -45px;
  &__item{
    margin: 0 10px 45px;
    display: flex;
    align-items: center;
    justify-content: center;


    background: #F9F9F9;
    box-shadow: -2px 3px 5px rgba(40, 72, 92, 0.1);
    border-radius: 10px;
    border: 1px solid transparent;
    padding: 5px;

    width: 190px;
    height: 55px;
    transition: $tr;

    & img{
      max-width: 100%;
      max-height: 100%;
    }
    &:hover{
      border-color: #168B9C;
      box-shadow: -2px 3px 5px rgba(40, 72, 92, 0.32);
    }
  }
}