
//image

@mixin img-size {
  width: 100%;
  min-height: 100%;
  object-fit: cover;
  object-position: center;
  transition: .5s;
}


@mixin img-cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}



// hide/show

@mixin hide {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

@mixin show {
  opacity: 1;
  pointer-events: auto;
  visibility: visible;
}



//flex

@mixin horizonCenter {
  display: flex;
  align-items: center;
}

@mixin horizonCenterBetween {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@mixin verticalCenter {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}



// Setting width/height

@mixin size($width, $height: $width) {
  width: $width;
  height: $height;
}
