
//normalize

*{position:relative;margin:0;padding:0;box-sizing:border-box}a,button,span{display:inline-block}input,textarea{display:block;width:100%}table{width:100%;border-collapse:collapse}a{background-color:transparent;text-decoration:none}img{border:none;max-width:100%;height:auto}a,button{outline:0;cursor:pointer}li{list-style:none}button{background:0 0;border:none}a,button,input,textarea{color:$black;font-weight:inherit;-webkit-appearance:none;transition:all .5s ease}:after,:before{box-sizing:border-box}textarea{resize:none;outline:0;border:none;-webkit-appearance:none;border-radius:0;background:0 0}input{outline:0;border:none;-webkit-appearance:none;border-radius:0;background:0 0}input[type=number]{-moz-appearance:textfield}input::-webkit-inner-spin-button,input::-webkit-outer-spin-button{-webkit-appearance:none}input[type=submit]{cursor:pointer;-webkit-appearance:none;display:inline-block}input[type=search]::-webkit-search-cancel-button{background:0 0;display:none}.text-align-left{text-align:left}.text-align-center{text-align:center}.text-align-right{text-align:right}.full-img img{width:100%}figure{text-align:center;margin-bottom:30px;margin-top:5px;font-size:.75em;opacity:.8}[hidden]{display:none!important}.img-full{width: 100%;}.clearfix{&:after{content: '';clear: both;display: block}}

//title


h1, .h1 {
  margin: 0;
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 130%;
  display: flex;
  align-items: center;
  color: #28475C;
}
h2, .h2 {
  margin: 0;
  font-style: normal;
  font-weight: bold;
  font-size: 42px;
  line-height: 100%;
  color: #168B9C;
}
h3, .h3 {
  margin: 0;
  font-style: normal;
  font-weight: 300;
  font-size: 28px;
  line-height: 130%;
  text-transform: lowercase;
  color: #168B9C;
}
h4, .h4 {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 130%;
  color: #28475C;
}
h5, .h5 {

  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 150%;
  color: #000000;

}
h6, .h6 {

}

//text

.text {
  margin-bottom: 30px;
  & > * + * {
    margin-top: 15px;
  }
}

//body

body {
  font-family: $font;
  color: $black;
  font-style: normal;
  font-weight: 400;
  background: $white;
  font-size: 16px;
  @media (max-width: 735px) {
    body {
      font-size: 14px;
    }
  }
}

.wrapper {
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  opacity: 0;
  visibility: hidden;
  pointer-events: none;

}

body{
  &.loaded{

  }
  &.loadedFirst{
    .wrapper{
      opacity: 1;
      visibility: visible;
      pointer-events: auto;
    }
  }
}

header {
  width: 100%;
}
main {
  flex: auto;
  display: block;
}
footer {
  margin: auto auto 0 auto;
  width: 100%;
}

.container,
.container-fluid {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  z-index: 2;
}

.container {
  max-width: 1290px;
}
.container-fluid {
  max-width: 1920px;
}


//btn



.btn-row {
  text-align: center;
  .btn {
    margin: 10px;
  }
}


.column {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 -20px;
  .column-2 {
    width: 50%;
    padding: 10px 20px;
  }
  .column-3 {
    max-width: 320px;
    width: 33.3%;
    padding: 20px;
  }
}

//fancybox
.fancybox-navigation {
  position: static;
}



//swiper
.swiper-button {
  outline: none;

  &.swiper-button-prev {
    transform: scaleX(-1);
  }
  &:before, &:after {
    display: none;
  }
}

.swiper-pagination {
  position: absolute;
  width: 100%;
  text-align: center;
  margin-top: 50px;
  .swiper-pagination-bullet {
    opacity: 1;
    outline: none;
    &.swiper-pagination-bullet-active {
      .symptoms{
        &__video{
          &-wrap{
            display: block;
          }
        }
      }
    }
  }
}


.list-pages{
  margin: 80px 0;
  &> * + *{
    margin-top: 20px;
  }
  a{
    font-size: 20px;
    &:hover{
      text-decoration: underline;
    }
  }
}

