

.footer{
  z-index: 10;
  background: #28475C;
  padding: 75px 0;
  .logo{
    width: 120px;
    height: 80px;
  }
  &__wrap{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
  }
  .menu{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    &> * + *{
      margin-left: 40px;
    }
    a{
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 22px;
      color: #FFFFFF;
      transition: $tr;
      &:hover{
        color: $yellow;
      }
    }
  }
  .button{
    &-header{
      padding: 8px 38px;
    }
  }
  &-dev{
    position: absolute;
    font-style: normal;
    font-weight: 300;
    font-size: 13px;
    line-height: 15px;
    color: #9BB0AE;
    right: 20px;
    bottom: -30px;
    a{
      color: #75CEF8;
      transition: $tr;
      &:hover{
        color: $yellow;
      }
    }
  }
  .logo{
    & img{
      max-height: 100%;
    }
  }
}