@media(max-width: 1430px){
  .advantages{
    &:before{
      background: #F8FAFB;
      height: 30%;
    }
  }
}



@media(max-width: 1300px){
  .about{
    &__txt{
      margin: 0 0 0 auto;
    }
  }

  .faq{
    &__item{
      max-width: 52%;
    }
    &__img{
      max-width: 45%;
    }

  }

  .swiper-button-prev, .swiper-button-next{
    display: none;
  }
}





@media(max-width: 1170px){
  .about{
    padding: 100px 0 160px;
    &__img{
      max-width: 50%;
    }
    &__txt{
      max-width: 45%;
      &> * + *{
        margin-top: 35px;
      }
    }
  }
