@media(max-width: 1100px){
  .why{
    padding: 75px 0 60px;
    .table-wrap{
      overflow-y: hidden;
      overflow-x: auto;
      width: calc(100% + 40px);
      margin: 0 -20px;
      padding: 0 0 10px 10px;
      .why__table{
        //width: 1070px;
        width: 900px;
        background: rgba(208, 208, 208, 0.15);
        box-shadow: 0px 4px 0px rgba(203, 203, 203, 0.05);
        border-radius: 20px;
        padding: 10px;
        margin: 10px 0 0 ;
        table{
          thead{
            tr{
              th{
                &:first-child{
                  width: 25% !important;
                }
              }
            }
          }
          tbody{
            tr{
              td{
                &:first-child{
                  width: 25% !important;
                }
              }
            }
          }
        }
      }
    }

    &--icons{
      display: block;
      text-align: center;
      margin: 20px 0;
    }
  }
}


@media(max-width: 991px){

  .header{
    padding: 20px 0 0 0;
    .wrap-nav{
      nav{
        &:before{
          content: '';
          position: absolute;
          background: url("../img/advantages/img-decor.png") center no-repeat;
          background-size: cover;
          width: 730px;
          height: 430px;
          top: 50%;
          left: 50%;
          transform: translateY(-50%) translateX(-50%);
        }
      }
    }
  }
  .footer{
    padding: 50px 0 70px;
    &__wrap{
      flex-direction: column;
      &> * + *{
        margin-top: 40px;
      }
      .logo{
        width: 100%;
        text-align: center;
      }

    }
  }


  .swiper-pagination{
    margin-top: 35px;
  }


  .advantages{
    padding: 80px 0 40px;


    &__wrap{

    }
    &__img{
      width: 100%;
      max-width: 520px;
      margin: 0 auto;
      &:before{
        background-size: contain;
        left: 45%;
      }
    }
    &__content{
      max-width: 100%;
      padding: 50px 0 0;
      .alert{
        margin: 20px 0;
      }
    }
    &__lower{
      .title-wrap{
        h2, h5{
          max-width: 65%;
        }
      }
    }

  }


  .open-nav {
    order: 2;
    display: block;
    width: 30px;
    height: 24px;
    z-index: 88888;
    span{
      position: absolute;
      width: 100%;
      height: 3px;
      left: 0;
      background: #000;
      border-radius: 100px;
      transition: .3s all ease-out;
      &:first-child{
        top: 0;
      }
      &:last-child{
        bottom: 0;
      }
      &:nth-child(2){
        top: 50%;
        transform: translateY(-50%);
      }
    }
    &.open{
      span{
        &:first-child{
          transform: rotate(45deg);
          top: 10px;
        }
        &:nth-child(2){
          opacity: 0;
        }
        &:last-child{
          transform: rotate(-45deg);
          top: 10px;
        }
      }
    }
  }
  .wrap-nav {
    display: block;
    position: fixed;
    width: 360px;
    max-width: 85%;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    overflow: auto;
    visibility: hidden;
    opacity: 0;
    padding: 30px;
    margin: 0;
    background: #fff;
    transform: translateX(100%);
    transition: all 0.5s ease;
  }
  .wrap-nav.open {
    visibility: visible;
    opacity: 1;
    transform: translateX(0);
    box-shadow: 0px -15px 30px 0px rgba(25, 16, 78, 0.75);
  }
  .wrap-nav--contacts {
    display: block;
    margin: 0 0 15px 0;
  }
  .overlay.open {
    display: block;
    position: fixed;
    z-index: 9998;
    background: #19104E;
    opacity: 0.5;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .wrap-nav .menu {
    margin: 80px 0 40px;
    display: block;
    &> * + *{
      margin: 25px 0 0;
    }
    a{
      font-size: 18px;
    }
  }


  h1, .h1{
    font-size: 35px;
  }
  h2, .h2{
    font-size: 30px;
  }
  h3, .h3{
    font-size: 24px;
  }
  h4, .h4{
    font-size: 20px;
  }
  h5, .h5{
    font-size: 18px;
  }

  .welcome{
    padding: 180px 0 65px;
    &__info{
      justify-content: space-between;
      &> * + *{
        margin: 0;
      }
    }
    &__text{
      max-width: 400px;
    }
    &__img{
      max-width: 50%;
    }
  }

  .about{
    padding: 100px 0;
    &__txt{
      &-title{
        span{
          font-size: 50px;
          b{
            font-size: 65px;
          }
        }
      }
    }
    &__block{
      margin: 60px 0 0;
    }
  }

  .symptoms{
    padding: 20px 0 140px;
  }
  .button{
    padding: 0 65px;
    &-header{
      padding: 8px 38px;
    }
  }

  .how{
    padding: 10px 0 60px;
    &__img{
      width: 100%;
      max-width: 100%;
      text-align: center;
      order: 1;
      margin: 30px 0 0 0;
    }
    &__item{
      max-width: 45%;
    }
  }
  .faq{
    &__img{
      display: none;
    }
    &__item{
      max-width: 100%;
    }
    &__wrap{
      margin-top: 40px;
    }

  }

  .buy{
    &__wrap{
      flex-direction: column-reverse;
    }
    &__set{
      width: 100%;
      justify-content: space-around;
    }
    &__title{
      width: 100%;
      max-width: 100%;
      padding: 30px 0 0 0;
      text-align: center;
      h5{
        margin: 20px 0;
      }
    }
    &__block{
      margin-top: 70px;
      &-content{
        width: calc(100% - 230px);
        padding-left: 50px;
      }
    }

  }
}



