#animation{
  position:absolute;
  -webkit-animation:linear infinite alternate;
  -webkit-animation-name: l-r;
  -webkit-animation-duration: 5s;
}

.why--icons{
  width: 100%;
  height: 18px;
  img{
    position: absolute;
    top: 50%;
    transform: translateY(-50%) translateX(-50%);
    left: 50%;
    animation: l-t-r 4s infinite;
    transition: .2s;
  }
}


@keyframes l-t-r{

  0%{
    left: 45%;
  }
  50%{
    left: 55%;
  }
  100%{
    left: 45%;
  }
}